.posts {
    border-radius: 10px;
    background: url('../../public/assets/mar.png');
    background-size: cover;
    width: 63%;
    height: auto;
    margin: 8% auto 4% auto;
}

.posts .post {
    display: flex;
    border-radius: 10px;
    width: 100%;
    height: 100%;
    background-color: #0505056A;
    padding: 5%;
}

.posts .post h1,
.posts .post p {
    color: white;
}

.posts .post .autor {
    position: absolute;
    display: flex;
    align-items: center;
}

.posts .post .autor img {
    width: 35px;
    height: 35px;
    border-radius: 50%;
}

.posts .post .autor p {
    margin-left: 5px;
    letter-spacing: 2px;
}

.posts .post .textos {
    margin-top: auto;
}

.posts .post .textos h1 {
    max-width: 70%;
    font-size: 73px;
    margin-top: 22.5%;
}

.posts .post .textos p {
    max-width: 65%;
    margin: 4% 0;
    font-size: 22px;
}

@media screen and (max-width: 1450px) {
    .posts .post .autor p {
        font-size: 14px;
    }
    .posts .post .textos h1 {
        font-size: 65px;
    }
    .posts .post .textos p {
        font-size: 16px;
    }
}

@media screen and (max-width: 1366px) {
    .posts {
        margin: 10% auto 4% auto;
    }
    .posts .post .autor img {
        width: 30px;
        height: 30px;
    }
    .posts .post .autor p {
        font-size: 14px;
    }
    .posts .post .textos h1 {
        font-size: 60px;
    }
    .posts .post .textos p {
        font-size: 15px;
    }
}

@media screen and (max-width: 1200px) {
    .posts .post .autor p {
        font-size: 12px;
    }
    .posts .post .textos h1 {
        font-size: 52px;
    }
    .posts .post .textos p {
        font-size: 14px;
    }
}

@media screen and (max-width: 1030px) {
    .posts .post .autor p {
        font-size: 12px;
    }
    .posts .post .textos h1 {
        font-size: 46px;
    }
    .posts .post .textos p {
        font-size: 13px;
    }
}

@media screen and (max-width: 880px) {
    .posts {
        margin: 14% auto 4% auto;
    }
    .posts .post .autor p {
        font-size: 11px;
    }
    .posts .post .textos h1 {
        font-size: 39px;
    }
    .posts .post .textos p {
        font-size: 11px;
    }
}

@media screen and (max-width: 690px) {
    .posts {
        margin: 16% auto 4% auto;
    }
    .posts .post .autor img {
        width: 23px;
        height: 23px;
    }
    .posts .post .autor p {
        font-size: 9px;
    }
    .posts .post .textos h1 {
        font-size: 32px;
    }
    .posts .post .textos p {
        font-size: 9px;
    }
}

@media screen and (max-width: 560px) {
    .posts {
        margin: 18% auto 4% auto;
    }
    .posts .post .autor img {
        width: 20px;
        height: 20px;
    }
    .posts .post .autor p {
        font-size: 8px;
