/*---------------------------------------------------------------*/

.ultimosposts {
    width: 100%;
    background-color: #EAEAEA;
    padding: 1%;
}

.ultimosposts h1 {
    text-align: center;
    font-size: 64px;
    padding: 4% 0;
}

.ultimosposts .lista {
    display: flex;
    flex-wrap: wrap;
}

.ultimosposts .boxes {
    display: flex;
    justify-content: center;
}

.ultimosposts .lista .postagem {
    width: 573px;
    height: 765px;
    border-radius: 10px;
    background-color: white;
    margin: 0 1% 2.5% 1%;
    max-width: 47%;
}

.ultimosposts .lista .postagem .post-textos {
    padding: 5%;
}

.ultimosposts .lista .postagem .post-textos a {
    border: 1px dotted #bbbbbb;
    border-radius: 15px;
    color: #3F51B5;
    padding: 5%;
    font-size: 22px;
}

.ultimosposts .lista .postagem img {
    width: 100%;
}

.ultimosposts .boxes .lista h2 {
    font-size: 16px;
    color: #33439B;
    font-weight: bold;
    letter-spacing: 1px;
}

.ultimosposts .boxes .lista h1 {
    font-size: 42px;
    text-align: left;
}

.ultimosposts .boxes .lista p {
    font-size: 20px;
    margin-bottom: 30px;
}

.ultimosposts .maislidas {
    display: block;
    max-width: 33%;
}

.ultimosposts .maislidas h1 {
    color: white;
    letter-spacing: 2px;
    border-radius: 10px 10px 0 0;
    background-color: #3F51B5;
    font-size: 27px;
}

.ultimosposts .lista2 .maislida {
    width: 100%;
    background-color: white;
    height: auto;
    padding: 5% 5% 1% 5%;
    display: flex;
}

.ultimosposts .lista2 .maislida .maislida-textos {
    display: block;
    padding: 4%;
}

.ultimosposts .lista2 .maislida img {
    width: 37%;
    height: 37%;
}

.ultimosposts .lista2 .maislida h2 {
    letter-spacing: 2px;
    font-size: 16px;
    color: #40BE65;
    margin-bottom: 15px;
}

.ultimosposts .lista2 .maislida p {
    color: #7B8591;
    font-size: 18px;
}

@media screen and (max-width: 1366px) {
    .ultimosposts h1 {
        font-size: 48px;
    }
    .ultimosposts .boxes .maislidas h1 {
        font-size: 24px;
    }
    .ultimosposts .lista .postagem {
        width: 410px;
        height: 573px;
    }
    .ultimosposts .boxes .lista h2 {
        font-size: 14px;
    }
    .ultimosposts .boxes .lista h1 {
        font-size: 32px;
        text-align: left;
    }
    .ultimosposts .boxes .lista p {
        font-size: 15px;
    }
    .ultimosposts .lista .postagem .post-textos a {
        font-size: 16px;
    }
    .ultimosposts .lista2 .maislida h2 {
        font-size: 14px;
    }
    .ultimosposts .lista2 .maislida p {
        font-size: 14px;
    }
}

@media screen and (max-width: 1100px) {
    .ultimosposts h1 {
        font-size: 36px;
    }
    .ultimosposts .lista .postagem h2 {
        font-size: 14px;
    }
    .ultimosposts .lista .postagem h1 {
        font-size: 25px;
    }
    .ultimosposts .lista .postagem p {
        font-size: 14px;
    }
    .ultimosposts .boxes .maislidas h1 {
        font-size: 17px;
    }
    .ultimosposts .boxes .maislidas h2 {
        font-size: 10.5px;
    }
    .ultimosposts .boxes .maislidas p {
        font-size: 9.5px;
    }
    .ultimosposts .lista .postagem {
        height: 490px;
    }
}

@media screen and (max-width: 880px) {
    .ultimosposts h1 {
        font-size: 32px;
        padding: 8% 0;
    }
    .ultimosposts .boxes {
        display: block;
    }
    .ultimosposts .lista .postagem {
        max-width: 80%;
        margin: auto;
        margin-bottom: 25px;
    }
    .ultimosposts .maislidas {
        max-width: 80%;
        margin: auto;
        margin-bottom: 20px;
    }
}

@media screen and (max-width: 600px) {
    .ultimosposts h1 {
        font-size: 26px;
    }
}